@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700;800;900&family=Roboto:wght@400;500;700&display=swap');

/*Global*/
body {
  margin: 0;
  background-color: #F1F5F8;
  font-family: 'Roboto',
  sans-serif;
}

/* Header */


.header{
  display: flex;
  align-items: center;
  height: 100px;
  background-color: #1a1a1d;
  padding: 10px 30px 10px 30px;
  position: fixed;
  width: 100%;
  z-index: 100;
  justify-content: space-between;
}

.left-header{
  width: 250px;
}

.non-textdecoration{
    color: white;
    text-decoration: none;
}

.left-header img{
  width: 250px;
}

.middle-header{
  position: relative;
  display: flex;
  flex: 1;
  max-width: 600px;
  align-items: center;
  margin-left: 15px;
  margin-right: 15px;
}

.search-bar{
  flex: 1;
  height: 48px;
  border-radius: 24px;
  border: none;
  padding-left: 15px;
  font-size: 16px;
}

.search-button{
  position: absolute;
  right:20px;
  bottom: 10px;
  cursor: pointer;
}

.right-header{
  display: flex;
  column-gap: 30px;
  font-size: 20px;
  font-weight: 500;
  color: white;
  margin-right: 100px;
}

/*Slider*/


.slide-container div{
  font-size: 21px;
  text-align: center;
  color: yellow;
  
}

.slide-container{
  padding-top: 130px;
  background-color: #e91919;
  padding-bottom: 11px;
}

/*Content*/
.product-container{
  display: flex;
  margin-top: 20px;
  margin-left: 20px;
  margin-bottom: 20px;
}

/*Filter*/
.filter-container{
  display: flex;
  flex-direction: column;
  row-gap: 20px;
  background-color: #1C1D25;
  padding: 20px;
  border-radius: 10px;
}

.filter-title{
  font-size: 20px;
  font-weight: 600;
  color: #E9EDF1;
}

.mangdidong-list{
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  column-gap: 10px;
  row-gap: 15px;
  color: #1C1D25;
}

.btn{
  padding: 10px 15px 10px 15px;
  font-size: 16px;
  background-color: #E9EDF1;
  border: none;
  border-radius: 25px;
  color: #1C1D25;
 }

.dauso-list{
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
  column-gap: 10px;
  row-gap: 15px;
  color:#1C1D25 ;
}

.kieuso-list{
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  column-gap: 10px;
  row-gap: 15px;
  color:#1C1D25;
}

.kieuso-kieuso-list{
  display: flex;
  flex-direction: column;
  row-gap: 10px;
  justify-content: center;
  align-items: center;
  color: #1C1D25;
  border-width: 1px;
  border-color: rgb(197, 197, 197);
  border-style: solid;
  border-radius: 5px;
  padding-top: 10px;
  padding-bottom: 10px;
  padding-left: 5px;
  padding-right: 5px;
}

.ten1{
  font-size: 23px;
  font-weight: 600;
  color: white;
}

.ten2{
  font-size: 13px;
  color: white;
}

/*Product*/
.productlist-container{
  display: flex;
  flex-direction: column;
  row-gap: 20px;
  background-color: white;
  padding: 20px;
  border-radius: 10px;
  margin-right: 20px;
  margin-left: 20px;
  flex: 1;
}

.productlist{
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  row-gap: 40px;
  column-gap: 20px;
}

@media (max-width:1400px) {
  .productlist {
    grid-template-columns: 1fr 1fr;
  }

}
@media (max-width:1000px) {
  .productlist {
    grid-template-columns: 1fr;
  }
}
@media (max-width:800px) {
  .filter-container {
    display: none;
  }
}

.product-img{
  width: 80px;
  display: flex;
  align-items: center;
}

.product-img img{
  width: 100%;
}

.product-detail{
  display: flex;
  column-gap: 25px;
  justify-content:center;
  align-items: center;
  border-style: solid;
  border-width: 1px;
  padding: 10px 15px 10px 15px;
  border-radius: 5px;
  border-color: rgb(195, 195, 195);
}

.product-text{
  display: flex;
  flex-direction: column;
  row-gap: 10px;
}

.tensp{
  font-size: 24px;
  font-weight: 700;
  color: #1C1D25;
}

.giasp{
  text-align: right;
  color: green;
  font-size: 18px;
}

/* Footer */
.bch{
  padding-top: 30px;
    padding-bottom: 30px;
    padding-left: 50px;
    background-color: #1C1D25;
    color: white;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

@media (max-width:1000px) {
  .bch {
    flex-direction: column;
  }
}

.bch-content{
  padding-right: 20px;
    float: left;
}

.bch-img{
  text-align: right;
  padding-right: 50px;
    
}
.bch-img img{
  width: 600px;
}

@media (max-width:800px) {
  .bch-img img {
    display: none;
  }
}


.hotline_footer{
  text-align: center;
  padding-top: 30px;
}

.img-footer{
  text-align: center;
}

.bch-end{
  padding-top: 20px;
  padding-bottom: 20px;
  padding-left: 50px;
  background-color: #1C1D25;
  color: white;
}

.end{
  text-align: center;
  padding-bottom: 20px;
}

.button {
  background-color: #04AA6D;
  color: white;
  align-items: center;
  padding: 14px 20px;
  margin: 8px 0;
  border: 20px;
  cursor: pointer;
  width: 200px;
}

.footer-container{
  display: flex;
  flex-direction: column;
}

/*News*/

.news-container{
  margin-left: 100px;
  margin-right: 100px;
  margin-top: 20px;
  margin-bottom: 20px;
}

.news-article{
  background-color: white;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  row-gap: 40px;
  column-gap: 20px;
  align-content: center;
  justify-items: center;
  padding: 20px;
}

@media (max-width:1200px) {
  .news-article {
    grid-template-columns: 1fr 1fr;
  }

}

@media (max-width:900px) {
  .news-article {
    grid-template-columns: 1fr;
  }

}

.article-image img{
  width: 100%;
}

.article-image{
  width: 300px;
}

.article-list{
  display: flex;
  flex-direction: column;
  width: 300px;
  row-gap: 10px;
  text-align: center;
}

.article-content{
  word-wrap: break-word;
}

/* Blog */

.blog-container{
  margin-left: 15%;
  margin-right: 15%;
  margin-bottom: 20px;
  padding: 20px;
  background-color: #E9EAEE;
}

.img-container{
  display: flex;
  justify-content: center;
}

.img_blog{
  width: 600px;
}

.img_blog img{
  width: 100%;
}

.blog{
  padding-left: 50px;
  padding-right: 50px;
  line-height: 30px;
}

.blog2{
  padding-top: 15px;
  padding-bottom: 10px;
  padding-left: 50px;
  padding-right: 50px;
  line-height: 30px;
}

.list-blog{
  padding-left: 50px;
}




